.csv-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  transform: scale(1);
  align-items: center;
  overflow-y: auto;
}

.csv-popup {
  background-color: var(--background-color);
  border-radius: 15px;
  max-width: 1100px;
  max-height: 90vh;
  overflow-y: auto;
  border: 4px solid var(--switch-outline);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.5);
}

.csv-popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 2px solid var(--table-outline);
  background-color: var(--background-color);
  position: sticky;
  top: 0;
  z-index: 10;
}

.csv-popup-header h2 {
  margin: 0;
  font-family: "Montserrat";
  font-size: 2rem;
}

.csv-popup-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text-color);
  padding: 5px;
}

.csv-popup-close:hover {
  color: white;
}

.csv-popup-content {
  padding: 5px;
  font-size: 20px;
}

.csv-popup-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
  justify-content: center;
}

.csv-popup-main {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 10px;
  padding: 0 auto;
  justify-content: center;
}

.ore-table-parent {
  flex: 1;
  max-height: 400px;
  overflow-y: auto;
}

.csv-input {
  flex: 1;
}

.csv-input textarea {
  width: 100%;
  height: 100%;
  max-height: 410px;
  resize: none;
  color: var(--text-color);
  background-color: var(--background-color);
  outline: 3px solid var(--switch-outline);
}

@media (max-width: 768px) {
  .csv-popup-main {
    flex-direction: column;
  }

  .csv-popup-buttons {
    flex-direction: column;
  }
}

.csv-usage {
  margin-top: 2vh;
  border: 3px solid var(--switch-outline);
  border-radius: 25px;
  background-color: var(--background-color);
  width: fit-content;
  padding-right: 50px;
}

.b-container {
  display: flex;
  gap: 1vw;
  flex-direction: row;
  margin-left: 5vw;
  margin-bottom: 1vw;
}

.main-container h1 {
  margin-left: 25px;
}

.main-container li {
  font-size: 18px;
}

.c-dropdown-container {
  position: relative;
  display: inline-block;
  margin-left: 0px;
}

.history-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
  border: 1px solid #444;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  width: 250px;
  flex-wrap: nowrap;
  max-height: 500px;
  overflow-y: auto;
  text-align: left;
}

.dropdown-item {
  padding: 8px 12px;
  cursor: pointer;
  font-size: 18px;
  border-bottom: 1px solid #444;
}

.dropdown-item:hover {
  background-color: #3a3a3a;
}

.dropdown-item:last-child {
  border-bottom: none;
}

.sortable-header {
  cursor: pointer;
  transition: background-color 0.3s;
  user-select: none;
}

.sortable-header:hover {
  background-color: #3a3a3a;
}

.positive-change {
  color: #4caf50;
  font-weight: bold;
}

.negative-change {
  color: #f44336;
  font-weight: bold;
}

.ore-changes-details {
  height: fit-content;
  width: fit-content;
  text-align: left;
  margin-top: -15px;
}
.ore-changes-details h3 {
  text-align: left;
  margin-bottom: -12px;
  font-size: 18px;
}

.ore-changes-list {
  border: 2px solid var(--switch-outline);
  overflow-y: auto;
  max-height: 135px;
  text-align: left;
}

.ore-changes-list li {
  text-align: left;
  margin-left: -30px;
  font-size: 18px;
  margin-right: 5px;
  margin-bottom: 3px;
}

.value-change-summary {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 15px;
  gap: 25px;
  background-color: var(--background-color);
  border-radius: 15px;
  width: fit-content;
  height: fit-content;
  outline: 3px solid var(--switch-outline);
}

.value-change-cards {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 20px;
  max-width: 100%;
  height: 200px;
}

.value-card {
  align-content: center;
  text-align: center;
  flex: 1;
  padding: 10px;
  border-radius: 10px;
}

.value-card.gained {
  background: rgba(76, 175, 80, 0.2);
  border: 2px solid #4caf50;
}

.value-card.lost {
  background: rgba(244, 67, 54, 0.2);
  border: 2px solid #f44336;
}

.value-card.net {
  background: rgba(33, 150, 243, 0.2);
  border: 2px solid #2196f3;
}

.value-card.positive span:last-child {
  color: #4caf50;
  font-weight: bold;
}

.value-card.negative span:last-child {
  color: #f44336;
  font-weight: bold;
}

.ore-table-parent {
  padding-top: 2vh;
  overflow: hidden;
  overflow-x: auto;
  overflow-y: scroll;
  height: 100%;
  width: 100%;
}

.ore-list table {
  height: 100%;
  width: 150%;
  font-size: 20px;
  border-collapse: collapse;
  background-color: var(--background-color);
  z-index: 0;
}

.ore-list td,
.ore-list th {
  border: 2px solid var(--csv-outline);
  padding: 3px;
  text-align: center;
}

.ore-list th {
  background-color: #232222;
  color: white;
  padding-top: 10px;
}

[data-theme="light"] {
  --csv-outline: black;
}

[data-theme="dark"] {
  --csv-outline: #9a9a9a;
}

.csv-input textarea {
  width: 95%;
  height: 95%;
  resize: vertical, horizontal;
  margin-bottom: 0px;
  font-size: 18px;
  border: 2px solid #000000;
  padding: 8px;
}

