.misc-page {
  padding-left: 3vw;
  padding-top: 3vh;
  font-family: "Comfortaa";
  display: flex;
  flex-direction: column;
  align-items: left;
  min-height: 100vh;
}

.cards-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: left;
  gap: 30px;
  width: 97%;
  margin-top: 5px;
  margin-left: 0px;
}

.card {
  width: fit-content;
  max-width: 100%;
  height: 500px;
  background-image: linear-gradient(
    135deg,
    #fdfcef 0%,
    #d3f8f8 20%,
    #faedfd 30%,
    #f2caff 37.5%,
    #ffffff 50%,
    #ffd9a1 62.5%,
    #edebd6 70%,
    #ecf9df 80%,
    #b7dce1 100%
  );
  border-radius: 20px;
  transition: all 0.3s;
  padding: 2px;
}

.card2 {
  width: 100%;
  height: 100%;
  background-color: var(--background-color);
  border-radius: 17px;
  transition: all 0.2s;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  color: var(--textCol);
}

[data-theme="light"] {
  --background-color: #ffffff;
  --textCol: #2f2f2f;
}

[data-theme="dark"] {
  --background-color: #121212;
  --textCol: #ffffff;
}

.card2:hover {
  transform: scale(0.98);
  border-radius: 20px;
}

.card:hover {
  box-shadow: 0px 0px 30px 1px
    linear-gradient(
      135deg,
      #fdfcef 0%,
      #d3f8f8 20%,
      #faedfd 30%,
      #f2caff 37.5%,
      #ffffff 50%,
      #ffd9a1 62.5%,
      #edebd6 70%,
      #ecf9df 80%,
      #b7dce1 100%
    );
}

.card2 h3 {
  margin-top: 0;
  color: var(--textCol);
}

.card2 p {
  flex-grow: 1;
  font-size: 14px;
}

.search-filters,
.numNV {
  max-height: 500px;
  overflow-y: auto;
  padding-right: 8px;
  margin-top: -5px;
  margin-bottom: 0px;
  font-size: 0.8em;
  line-height: 1.4;
}

.charms-box {
  margin-top: -15px;
  overflow-y: auto;
  max-width: 1000px;
  padding-right: 8px;
  font-size: 0.8em;
  line-height: 1.4;
  margin-bottom: 0px;
}

.charms-table {
  margin-top: 15px;
}

.description-column {
  text-align: left;
  padding-left: 5px;
  font-size: 18px;
}

.search-filters p {
  margin: 0.3em 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.search-filters strong {
  font-weight: bold;
  color: var(--accent-color);
}

.copy-btn {
  background: none;
  border: none;
  color: var(--accent-color);
  cursor: pointer;
  font-size: 0.9em;
  padding: 2px 5px;
  opacity: 1;
  transform: scale(2);
  transition: opacity 0.2s;
}

.copy-btn:hover {
  opacity: 1;
}

.numNV::-webkit-scrollbar {
  width: 12px;
}

.numNV::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 2px;
}

.mode-selector {
  transform: scale(1);
  display: flex;
  gap: 10px;
  margin-bottom: 5px;
}

.ore-list {
  width: fit-content;
  margin-top: 5px;
  margin-left: 5px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
}

.ore-item {
  width: 250px;
  justify-content: space-between;
  display: flex;
  padding: 8px 12px;
  background: var(--background-color);
  outline: 1px solid var(--accent-color);
  border-radius: 8px;
}

.ore-name {
  color: var(--accent-color);
}

.m-ore-stats {
  color: var(--textCol);
}
