.welcome-page * {
  box-sizing: border-box;
}

.welcome-page .sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.welcome-page .hide {
  display: none;
}

.welcome-page {
  background-color: transparent;
  margin: 0;
  color: #212121;
  font-family: "Comfortaa";
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 0;
}

.welcome-page .square {
  padding: 5vw;
  width: 90vw;
  height: 85vh;
  transform: translate3d(-50%, -50%, 0);
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0.5;
  border-radius: 25px;
  background: var(--background-color);
  z-index: 1 !important;
}

.welcome-page .wcard {
  width: 100%;
  height: 100%;
  background-color: transparent;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 100;
}

.welcome-page .button-container {
  position: fixed;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  z-index: 9999;
}

.welcome-page .title {
  top: 15vh;
  font-size: 4vw;
  letter-spacing: 0em;
  font-weight: 900;
  overflow: hidden;
  color: var(--welcome-text);
  position: relative;
  max-width: 60vw;
}

.welcome-page .name,
.welcome-page .copyright {
  text-transform: uppercase;
  color: var(--welcome-text);
  position: absolute;
  left: 50%;
  margin: 0;
  padding: 0;
  transition: all 0.1s cubic-bezier(0.46, 0.03, 0.52, 0.96);
  transform: translate3d(-50%, 0, 0);
  font-weight: 800;
  overflow: hidden;
  font-size: 1.25vw;
  letter-spacing: 0.05em;
}

:root {
  --welcome-text: #000000;
}

[data-theme="dark"] {
  --welcome-text: #ffffff;
}

.welcome-page .name {
  top: 2.2vh;
}

.welcome-page .copy-wrap {
  font-family: "Segoe UI";
  display: block;
  text-align: center;
  letter-spacing: 1px;
  margin-top: 0px;
  transition: all 0.2s 0s ease-in-out;
  position: relative;
}

.welcome-page .title .copy-wrap {
  font-family: "Caprasimo";
}

.welcome-page .copyright {
  font-family: "Segoe UI";
  bottom: 2vh;
  overflow: hidden;
  height: 5vh;
  line-height: 6vh;
  position: fixed;
}

.welcome-page .copyright .copy-wrap:before {
  content: "";
  width: 15px;
  height: 2px;
  background-color: currentColor;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}
