.settings-panel {
  position: fixed;
  top: 0;
  right: -40vw;
  width: 40vw;
  height: 100vh;
  background-color: var(--background-color);
  opacity: 1;
  box-shadow: -5px 0px 25px 1px #000000;
  color: var(--text-color);
  z-index: 10000;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  opacity: 0;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
}

.settings-panel.open {
  right: 0;
  opacity: 1;
  border-left: 3px solid var(--switch-outline);
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.settings-title {
  margin: 0;
}

.settings-section {
  margin-bottom: 5px;
}

.section-title {
  color: var(--accent-color);
  font-family: "Montserrat";
  border-bottom: 1px solid #444;
  padding-bottom: 5px;
}

.theme-control {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 17px;
}

.opacity-control {
  margin-bottom: 20px;
}

.opacity-label {
  display: block;
  margin-bottom: 10px;
}

.opacity-slider {
  width: 100%;
}

.file-input {
  display: none;
}

.upload-button {
  display: block;
  padding: 10px;
  background-color: #333;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
  margin-bottom: 15px;
  color: white;
}

.upload-button:hover {
  background-color: #555;
}

.action-buttons {
  margin-bottom: 15px;
  display: flex;
  gap: 10px;
  width: 80%;
}

.apply-button {
  max-width: 150px;
  flex: 1;
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: "Comfortaa";
}

.reset-button {
  flex: 1;
  padding: 10px;
  max-width: 150px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: "Comfortaa";
}
.reset-button:hover {
  background-color: #ff5555;
}

.hotkey-info {
  margin-top: 20px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.hotkey-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  margin-top: 10px;
}

.hotkey-list p {
  margin: 5px 0;
  display: flex;
  align-items: center;
}

.hotkey-list strong {
  display: inline-block;
  width: 20px;
  text-align: center;
  background: var(--button-bg);
  padding: 2px 5px;
  border-radius: 4px;
  margin-right: 8px;
}
